import React, { Component } from 'react';
import Navigation from './Navigation';

class HomeHeader extends Component {
    render() {
        return (
            <div>
                <a href="/"><img src="images/logo.gif" alt="animal" /></a>
                <h2 itemProp="name">BLOG CHANH DY</h2>
                <h5 itemProp="author">by John Lemon</h5>
                <p className="me-intro" itemProp="description">Ni ti tha sc tp tnh vit lch,<br /> than th chuyn i v...<br /> ni xu ngi khc</p>

                <Navigation />

                <div className="search">
                    <form action="https://www.google.com/search" method="get">
                        <input type="text" name="q" placeholder="Tm ci gi?" />
                        <input type="hidden" name="as_sitesearch" value="blogchanhday.com" />
                    </form>
                </div>
            </div>
        );
    }
}

export default HomeHeader;


